/* Main Screen Recorder Styles */
.screen-recorder {
  min-height: 100vh;
  background: var(--brand-gradient);
  color: var(--text-inverse);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  transition: var(--theme-transition);
}

/* Remove old header styles - now handled by DynamicHeader */

.recorder-content {
  display: grid;
  grid-template-columns: 1fr 350px;
  gap: 2rem;
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
}

.recorder-main {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/* Mode Toggle Tabs */
.mode-toggle-tabs {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-bottom: 0.5rem;
}

.mode-tab {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  padding: 1rem 1.5rem;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.mode-tab:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
  color: white;
  transform: translateY(-2px);
}

.mode-tab.active {
  background: white;
  border-color: white;
  color: #667eea;
}

.mode-tab:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.mode-tab svg {
  width: 20px;
  height: 20px;
}

.recorder-sidebar {
  display: flex;
  flex-direction: column;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .recorder-content {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .recorder-sidebar {
    order: -1;
  }
}

@media (max-width: 768px) {
  .recorder-content {
    padding: 1rem;
  }
}

/* Card Styles */
.card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #333;
}

.card h3 {
  margin: 0 0 1rem 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: #2d3748;
}